}


// Post page 

.article-journal {
	margin: 30px auto;
	max-width: 740px;
}

body article header.journal-header {
	position: relative;
	max-width: 640px;

	img {
		object-fit: fill;

	}
}

.journal-title a {
	background: #248A83;
	position: absolute;
	top: 40px;
	left: 0;
	color: white;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 34px;
	width: 450px;
	padding: 5px 20px;
}

.journal-meta {
	position: absolute;
	background: #248A83;
	bottom: 7px;
	right: 0;
	color: white;
	padding: 2px 30px;
	margin: 0;
}

.journal-content {
	max-width: 640px;
	text-align: justify;
}

.journal-page-read-more {
	a {
		padding: 10px;
		text-transform: uppercase;
		text-decoration: none;
		border: 1px solid #000;
		color: #000;

		&:hover {
			background: #248A83;
			color: white;
		}
	}
}

